*, :before, :after {
  box-sizing: border-box;
}

:root {
  --pink: #e86f54;
  --pink-hover: #e25b41;
  --pink-focus: #fa6f54;
  --red: #e74a3b;
  --green: #3cbc81;
  --border: #d7d7d7;
  --border-hover: #111111b2;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.modal {
  max-width: 272px;
  width: 95%;
  min-height: 357px;
  background: #fcfcfc;
  border-radius: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 2px 1px #0003, 0 1px 3px #0000001f, 0 1px 1px #00000024;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0000004d;
  padding: 90px 24px 24px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.close-btn {
  background-color: #0000;
  border: none;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal-center {
  padding: 28px 16px;
}

.modal-input {
  width: 100%;
  height: 35px;
  border: 1px solid var(--border);
  border-radius: 4px;
  outline: none;
  margin: 0 auto;
  padding-left: 12px;
}

.modal-input:hover {
  border-color: var(--border-hover);
  cursor: pointer;
}

.modal-input:not(:placeholder-shown):required:valid:focus-within {
  border-color: var(--green);
}

.modal-input:not(:placeholder-shown):required:invalid {
  border-color: var(--red);
}

.modal-text {
  padding-top: 14px;
  padding-bottom: 18px;
}

.modal-label {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.modal-break {
  padding-top: 14px;
  padding-bottom: 24px;
}

.modal-button {
  color: #fff;
  text-align: center;
  width: 100%;
  height: 37px;
  width: 240px;
  background: var(--pink);
  cursor: pointer;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.modal-button:hover {
  background: var(--pink-hover);
}

.modal-button:focus {
  background: var(--pink-focus);
  box-shadow: 0 4px 16px #fa6f5433;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--pink);
  z-index: 999;
  justify-content: center;
  padding: 32px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
  box-shadow: 0 1px 6px #2e2f4214, 0 1px 1px #2e2f4229, 0 2px 1px #2e2f4214;
}

.menu-container.is-open {
  transform: translateX(0);
}

.modal-menu {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 26px;
  display: flex;
}



